import React, {Component} from 'react';
import {render} from 'react-dom';
import Modal from '../js/index';
import '../sass/modal.scss';
import './sass/example.scss';

class ModalPopUp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible1: false,
      visible2: false,
      animation: false,
      maskAnimation: false,
      hideAllModal: false
    };
  }

  handleChange = () => {
    this.setState({
      animation: !this.state.animation,
      maskAnimation: !this.state.maskAnimation
    });
  };

  onClick = (index) => {
    return () => {
      this.setState({
        [`visible${index}`]: true
      });
    }
  };

  onClose = (index) => {
    return () => {
      if (index === 2) {
        this.setState({
          visible2: false,
          hideAllModal: false
        });
      } else {
        this.setState({
          visible2: false,
          visible1: false,
          hideAllModal: true
        });
      }
    }
  };

  renderModal1() {
    const {visible1, animation, maskAnimation} = this.state;

    return (
      <Modal
        visible={visible1}
        onClose={this.onClose()}
        title=""
        style={{width: '500px'}}
        bodyStyle={{height: '300px'}}
        footer={(
          <div>
            <button className="example-btn example-btn-primary" onClick={this.onClick(2)}>
              
            </button>
          </div>
        )}
        animation={animation}
        maskAnimation={maskAnimation}
      >
        <p></p>
        <p></p>
      </Modal>
    );
  }

  renderModal2() {
    const {visible2, animation, maskAnimation, hideAllModal} = this.state;

    return (
      <Modal
        visible={visible2}
        onClose={this.onClose(2)}
        title=""
        style={{width: '400px'}}
        bodyStyle={{height: '200px'}}
        footer={(
          <div>
            <button className="example-btn example-btn-primary" onClick={this.onClose(2)}>
              
            </button>
            <button className="example-btn example-btn-primary" onClick={this.onClose()}>
              
            </button>
          </div>
        )}
        animation={animation}
        maskAnimation={maskAnimation}
        hideAllModal={hideAllModal}
      >
        <p></p>
        <p></p>
        <p></p>
      </Modal>
    );
  }

  render() {
    const {animation} = this.state;

    return (
      <div className="example">
        <h3></h3>
        <p>
          <input type="checkbox" checked={animation}
                 onChange={this.handleChange}/></p>
        <ol className="example-list">
          <li>
            <button className="example-btn example-btn-default"
                    onClick={this.onClick(1)}>
              
            </button>
          </li>
        </ol>
        {this.renderModal1()}
        {this.renderModal2()}
      </div>
    );
  }
}

render(
  <ModalPopUp />, document.getElementById('layout')
);



